import * as React from 'react';

import Staff, { API } from '@miq/staffjs';
import { SharedDataCtx } from '@miq/contextjs';

import { Response } from '@miq/utiljs';
import { useDocumentTitle, useRequest } from '@miq/hookjs';
import { Loading } from '@miq/componentjs';

export default function ShopNotificationView() {
  useDocumentTitle('Notifications');

  const { isLoaded, site = {}, ...rest } = React.useContext(SharedDataCtx);
  const settingSlug = site.settings;

  const { res, loading } = useRequest(() => API.get(`shopy-analytics/${settingSlug}/notifications/`));

  if (!isLoaded) return null;
  if (loading) return <Loading />;

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const { has_issues, issues } = rest;

  console.log(has_issues, issues);

  return (
    <Staff.View back="/staff/store/">
      <div>No not.ifications here ...</div>
    </Staff.View>
  );
}
